let fontUnit = 'upx'

fontUnit = 'rpx'

export default {
	name: 'BjxInputs',
	props: {
		placeholder: {
			type: String,
			default: ''
		},
		value: {
			type: String,
			default: ''
		},
		itemKey: {
			type: String,
			default: ''
		},
		width: {
			type: String,
			default: '600'
		},
		list: Function,
	},
	data() {
		return {
			timer: null,
			data: [],
			isShow: false,
			theValue: this.value,
		}
	},
	computed: {
		theWidth() {
			return !isNaN(Number(this.width)) ? this.width + fontUnit : this.width
		},
		show(){
			return this.isShow&&this.data.length
		}
	},
	methods: {
		theFocus(){
			this.isShow = this.data.length > 0
		},
		theInput(e) {
			if(this.timer) {
				clearTimeout(this.timer)
			}
			this.timer = setTimeout(()=>{
				this.theValue = e.detail.value
				this.$emit('list',{value: this.theValue,callback: this.setData})
			},300)
		},
		getItem(item) {
			return this.itemKey && typeof item == 'object' ? item[this.itemKey] : item
		},
		selectItem(item) {
			this.isShow = false
			this.theValue = this.itemKey ? item[this.itemKey] : item
			this.$emit('select',item)
		},
		setData(data) {
			if(data && data.length) {
				this.isShow = true
				this.data = data
			}
		},
		setValue(value){
			this.theValue = value
		}
	},
}
